<template>
	<view class="content" :style="'height:' + windowHeight + 'px;'">

		<view class="contentView">
			<view class="typeView">
				<text class="typeLabel" :style="isSel ? 'font-weight: bold;color: #323232' : 'color: #989898'"
					@click="typeSelect(1)">可使用({{nums}})</text>
				<text class="typeLabel" :style="!isSel ? 'font-weight: bold;color: #323232' : 'color: #989898'"
					@click="typeSelect(0)">不可用({{noNums}})</text>
			</view>
			<view class="exchangeView" v-if="isSel">
				<view class="exchange-back">
					<input type="text" :value="couponNum" class="exchange-input" placeholder="输入兑换码"
						@input="inputCode" />
					<view class="exchange-btn" @click="doExahnge()">
						兑换
					</view>
				</view>
			</view>
			<scroll-view scroll-y="true" class="scroll" :style="'height:' + scrollHeight + 'px;'"
				:refresher-triggered="refresherTriggered" @scrolltolower="scrolltolower" :refresher-enabled="true"
				:refresher-threshold="45" @refresherrefresh="refresherrefresh">
				<image src="../../../static/mine/coupon-no.png" mode="aspectFit" style="width: 240px;height: 176px;margin-left: calc(50% - 120px);margin-top: 70px;" v-if="couponArray.length == 0 && isSel"></image>
				<image src="../../../static/mine/coupon-no.png" mode="aspectFit" style="width: 240px;height: 176px;margin-left: calc(50% - 120px);margin-top: 70px;" v-if="couponNoA.length == 0 && !isSel"></image>
				
				
				<view class="couponCell" v-for="(item,index) in (isSel ? couponArray : couponNoA)" :key="index">
					<view class="coupon-leftView">
						<text class="coupon-leftView-typeL"
							:style="isSel? 'color:#7900B2' : 'color:#989898'" v-if="item.isall == 1"> 平台通用券 </text>
							<text class="coupon-leftView-typeL"
								:style="isSel? 'color:#7900B2' : 'color:#989898'" v-else> 平台专用券 </text>
						<view class="coupon-leftView-priceView">
							<text class="coupon-leftView-priceL" style="font-size: 44rpx;line-height:108rpx;"
								:style="isSel? 'color:#7900B2' : 'color:#989898'">¥</text>
							<text class="coupon-leftView-priceL"
								:style="isSel? 'color:#7900B2' : 'color:#989898'">{{item.limit}}</text>
						</view>
					</view>
					<image src="../../../static/order/cou-xuxian.png" mode="" class="lineImageView"></image>
					<view class="coupon-MiddelView">
						<text class="coupon-MiddelView-nameL"
							:style="isSel? 'color:#7900B2' : 'color:#989898'">{{item.text}}</text>
						<text class="coupon-MiddelView-msgL"
							:style="isSel? 'color:#7900B2' : 'color:#989898'">{{item.end_time}}</text>
						<text class="coupon-MiddelView-msgL oneline"
							:style="isSel? 'color:#7900B2' : 'color:#989898'">{{item.text_limit}}</text>
					</view>
					<view class="coupon-select_image" v-if="isSel" @click="doSelectCoupon(item,index)">
						去使用
					</view>

				</view>
			</scroll-view>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				windowHeight: 0,
				scrollHeight: 0,
				couponArray: [],
				couponNoA: [],
				nums: 0,
				noNums: 0,
				isSel: 1,
				selectIndex: 999999,
				couponNum: '',
				page: 1,
				refresherTriggered: 0
			}
		},
		onLoad() {
			this.windowHeight = getApp().globalData.windowHeight - getApp().globalData.statusBarHeight - 44
			this.scrollHeight = this.windowHeight - 110
			this.getCouponNums()
			this.getCouponList()
			// this.nums = 3
			// this.noNums = 6
			// this.couponArray = ['平台通用券 ¥6', '平台通用券 ¥6', '平台通用券 ¥6', '平台通用券 ¥6', '平台通用券 ¥6', '平台通用券 ¥6', '平台通用券 ¥6',
			// 	'平台通用券 ¥6'
			// ];
			// this.couponNoA = ['', '', '']
			// this.couponArray = this.couponArray.concat(this.couponNoA)
			// console.log(this.couponArray)
		},
		methods: {
			typeSelect(e) {
				this.isSel = e
				if (this.isSel) {
					this.scrollHeight = this.windowHeight - 110
				} else {
					this.scrollHeight = this.windowHeight - 40
				}
				this.page = 1
				this.getCouponList()
			},
			doSelectCoupon(item, index) {
				uni.switchTab({
					url: '../../index/index'
				});
			},
			inputCode(e) {
				this.couponNum = e.detail.value
			},
			doExahnge() {
				var that = this
				if (that.couponNum.length > 0) {
					that.http.sendRequest('Couponuser.Exchange', {
						'code': that.couponNum
					}).then(function(res) {
						if (res.code == 0) {
							that.couponNum = ''
							that.getCouponNums()
							that.page = 1
							that.isSel = 1
							that.getCouponList()
						}
					})
				}

			},
			getCouponList() {
				var that = this

				that.http.sendRequest('Couponuser.GetList', {
					'usetype': that.isSel ? '1' : '2',
					'money': 0,
					'p': that.page,
					'type':0
				}).then(function(res) {
					if (res.code == 0) {
						if (that.isSel) {
							if (that.page == 1) {
								that.couponArray = res.info
							} else {
								that.couponArray = that.couponArray.concat(res.info)
							}
						} else {
							if (that.page == 1) {
								that.couponNoA = res.info
							} else {
								that.couponNoA = that.couponNoA.concat(res.info)
							}
						}

					}
				})


			},
			getCouponNums() {
				var that = this
			
				that.http.sendRequest('Couponuser.GetNums', {
					'money': 0,
					'type': 0
				}).then(function(res) {
					if (res.code == 0) {
						that.nums = res.info[0].ok
						that.noNums = res.info[0].no
					}
				})
			
			
			},
			scrolltolower() {
				console.log('滚动到底部');
				var that = this
				that.page++
				that.getCouponList()
			},
			refresherrefresh() {
				var that = this
				that.page = 1

				that.refresherTriggered = true;
				that.getCouponList()
				console.log('自定义下拉刷新被触发');
				setTimeout(() => {
					that.refresherTriggered = false;
				}, 1000);
			},
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
	}

	.content {
		width: 100%;
		height: 100%;
		background-color: #F7F8F9;
	}

	.contentView {
		display: flex;
		flex-direction: column;
		/* top: 54px; */
		bottom: 0px;
		width: 750rpx;
		left: 0;
		height: 100%;
	}

	.typeView {
		width: 750rpx;
		height: 80rpx;
		display: flex;
		flex-direction: row;
	}

	.typeLabel {
		width: 375rpx;
		text-align: center;
		height: 80rpx;
		line-height: 80rpx;
		font-size: 32rpx;
		color: #989898;
		margin-left: 0;
	}

	.exchangeView {
		margin-left: 20rpx;
		height: 70px;
		width: calc(100% - 20px);
		/* background-color: #007AFF; */
		flex-shrink: 0;
	}

	.exchange-back {
		margin-left: 10px;
		margin-top: 16px;
		height: 38px;
		border-radius: 19px;
		background-color: #FFFFFF;
		width: calc(100% - 20px);
		overflow: hidden;
		flex-direction: row;
		position: relative;
	}

	.exchange-input {
		margin-left: 10px;
		width: calc(100% - 85px);
		height: 100%;
		font-size: 15px;
		line-height: 38px;
		/* background-color: #007AFF; */
	}

	.exchange-btn {
		position: absolute;
		top: 0;
		right: 0;
		width: 63px;
		height: 38px;
		line-height: 38px;
		background-color: #7900B2;
		color: #FFFFFF;
		font-size: 15px;
		text-align: center;
	}

	.scroll {
		margin-left: 20rpx;
		width: 710rpx;
		/* height: 480rpx; */
		margin-top: 10rpx;
		/* position: absolute; */
		/* background-color: #FF5625; */
		/* flex-wrap: wrap; */
		/* white-space: nowrap;
		display: flex;
		justify-content: flex-start; */
	}

	.couponCell {
		width: 680rpx;
		height: 160rpx;
		margin-left: 15rpx;
		margin-bottom: 20rpx;
		border-radius: 10rpx;
		background-color: #FEF4F2;
		position: relative;
		display: flex;
		flex-direction: row;
		overflow: hidden;
	}

	.zhanweiWhite {
		position: absolute;
		left: -10rpx;
		top: 70rpx;
		height: 20rpx;
		width: 20rpx;
		border-radius: 10rpx;
		background-color: #FFFFFF;
	}

	.coupon-leftView {
		width: 160rpx;
		height: 160rpx;

	}

	.coupon-leftView-typeL {
		position: absolute;
		width: 124rpx;
		height: 32rpx;
		line-height: 32rpx;
		font-size: 20rpx;
		text-align: center;
		color: #7900B2;
		border-radius: 8rpx 0 8rpx 0;
		background-color: #FFDAD3;
	}

	.coupon-leftView-priceView {
		margin-top: 32rpx;
		width: 160rpx;
		height: 96rpx;
		display: flex;
		flex-direction: row;
		/* align-items: flex-end; */
		justify-content: center;
	}

	.coupon-leftView-priceL {
		height: 96rpx;
		line-height: 96rpx;
		font-size: 64rpx;
		font-weight: bold;
		text-align: center;
		color: #7900B2;
	}

	.lineImageView {
		margin-left: 0;
		margin-top: 0px;
		height: 160rpx;
		width: 2rpx;
		/* background-color: #007AFF; */
	}

	.coupon-MiddelView {
		margin-left: 0;
		margin-top: 0px;
		height: 160rpx;
		width: 300rpx;
		/* background-color: #007AFF; */
		display: flex;
		flex-direction: column;
	}

	.coupon-MiddelView-nameL {
		margin-left: 20rpx;
		margin-top: 20rpx;
		height: 40rpx;
		line-height: 40rpx;
		color: #7900B2;
		/* width: 240rpx; */
		font-size: 28rpx;
		font-weight: bold;
	}

	.coupon-select_image {
		position: absolute;
		right: 15px;
		width: 66px;
		height: 26px;
		margin-top: 27px;
		border-radius: 13px;
		border: 1px solid #7900B2;
		color: #7900B2;
		line-height: 26px;
		text-align: center;
		font-size: 14px;
	}

	.coupon-MiddelView-msgL {
		margin-left: 20rpx;
		margin-top: 0px;
		height: 40rpx;
		line-height: 40rpx;
		color: #7900B2;
		font-size: 24rpx;
	}
</style>
